<template>
  <div class="dispath-style">
    <div class="bottom-left">
      <div class="bottom-left-left border-style2">
        <div class="title">业务量/超时回单量</div>
        <div
          class="charts-style"
          v-loading="!leftRes"
          element-loading-background="rgba(0, 0, 0, 0.2)"
          element-loading-text="加载中"
        >
          <div class="right">
            <bda-bar-charts
              v-if="leftRes"
              :barType="barType"
              :chartsConfig="chartsConfig"
              :barWidth="4"
              :legendStatus="true"
              :yAxiosDw="''"
            ></bda-bar-charts>
          </div>
          <div class="left">
            <div class="left-main">
              <div v-for="(v, k, i) in dataMap" :key="i" class="kb-style-main">
                <div class="kb-title">{{ k }}</div>
                <!--                                <div class="kb-value">{{parseInt(v).toLocaleString()}}</div>-->
                <div class="kb-value">
                  <span>{{ parseInt(v) }}件</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="bottom-left-right border-style2">
        <div class="title">主城渝西回单及时率</div>
        <div
          class="charts-style"
          v-loading="!fgsRes"
          element-loading-background="rgba(0, 0, 0, 0.2)"
          element-loading-text="加载中"
        >
          <bda-bar-charts
            v-if="fgsRes"
            :chartsConfig="fgsData"
            :barWidth="6"
            :legendStatus="false"
            :yAxiosDw="dw"
            :xVertical="true"
            :percentRange="[80, 100]"
          ></bda-bar-charts>
        </div>
      </div>
    </div>
    <div class="bottom-right border-style2">
      <div class="title">渝东南北回单及时率</div>
      <div
        class="charts-style"
        v-loading="!zrgsRes"
        element-loading-background="rgba(0, 0, 0, 0.2)"
        element-loading-text="加载中"
      >
        <bda-bar-charts
          v-if="zrgsRes"
          :chartsConfig="zrgsData"
          :barWidth="6"
          :legendStatus="false"
          :yAxiosDw="dw"
          :percentRange="[80, 100]"
          :xVertical="true"
        ></bda-bar-charts>
      </div>
    </div>
  </div>
</template>

<script>
import Interface from '../../../base/mixin/service-index-control';
import qs from 'qs';
import bdaBarCharts from '../../../widgets/bda-3d-barCharts/bda-3d-barCharts';

export default {
  name: "dispathBottom",
  mixins: [Interface],
  components: { bdaBarCharts },
  data() {
    return {
      dataMap: {
        '工单总量': 0,
        '超时回单量': 0
      },  // 看板总量数据
      barRes: false,
      barType: 'stack',
      chartsConfig: {},
      countMap: {},    //超时业务量与业务总量的占比统计
      fgsRes: false,
      zrgsRes: false,
      fgsData: {},
      zrgsData: {},
      dw: '%',
      leftRes: false,
    }
  },
  watch: {},
  created() {
  },
  mounted() {
    let $this = this;
    this.getAckOrdersBusinessAndOvertime(qs.stringify({
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams
    })).then(res => {
      this.leftRes = true;
      window.console.log(res, 'res');

      $this.ackOrdersBussinessAndOvertimeData(res);
    }).catch(() => {
      this.leftRes = true
    });
    this.getAckOrdersForFiliale(qs.stringify({
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams
    })).then(res => {
      this.ackOdersForData(res, '1');
    }).catch(() => {
      this.fgsRes = true;
    });
    //getSendOrdersForLLC
    this.getAckOrdersForLlc(qs.stringify({
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams
    })).then(res => {
      this.ackOdersForData(res, '2');
    }).catch(() => {
      this.zrgsRes = true;
    });

  },
  methods: {
    ackOrdersBussinessAndOvertimeData(data) {
      if (data) {
        if (data.total) {
          this.dataMap['工单总量'] = data.total.ccsum !== undefined ? data.total.ccsum : 0;
          this.dataMap['超时回单量'] = data.total.ccovertimesum !== undefined ? data.total.ccovertimesum : 0;
        }
        if (data.type) {
          let dimension = []
          let quotaMap = {
            '超时回单量': [],
            '各业务总量': [],
          }
          data.type.forEach(e => {
            dimension.push(e.busisubtype)
            quotaMap['各业务总量'].push(e.ccsum)
            quotaMap['超时回单量'].push(e.ccovertimesum)
            // quotaMap['各业务总量'].push(Math.round(e.ccsum / (e.ccsum + e.ccovertimesum) * 100 * 100) / 100)
            // quotaMap['超时派发量'].push(Math.round(e.ccovertimesum / (e.ccsum + e.ccovertimesum) * 100 * 100) / 100)
          })

          this.chartsConfig = {
            colorMap: { '超时回单量': ['#00FEBB', '#00BDFA'], '各业务总量': ['#00E2FF', '#0074FF'] },
            dimension: dimension,
            quotaMap: quotaMap,
          }
          //   for (let item of data.type) {
          //     let _obj = {
          //       ccovertime: item.ccovertimesum !== (undefined || null) ? item.ccovertimesum : 0,
          //       cc: item.ccsum !== (undefined || null) ? item.ccsum : 0,
          //       name: item.busisubtype ? item.busisubtype : ''
          //     };
          //     _obj.zb = (parseFloat(_obj.ccovertime / _obj.cc).toFixed(3)) * 100 + '%';
          //     if (_obj.name) {
          //       this.countMap[_obj.name] = _obj;
          //     }
          //   }

        }
      }
    },
    ackOdersForData(data, type) {
      let xData = [], dataInfo = [];
      if (data) {
        for (let item of data) {
          dataInfo.push(item.per);
          // xData.push(item.orgname ? item.orgname.replace('江津区', '江津') : '');
          xData.push(item.orgname);

        }
      }
      if (type === '1') {
        this.fgsRes = false;
        this.fgsData = {
          dimension: xData,
          quotaMap: { '供电分公司派单及时率': dataInfo },
          colorMap: { '供电分公司派单及时率': ['#00E2FF', '#0074FF'] }
        };
        this.$nextTick(() => {
          this.fgsRes = true;
        })
      } else if (type === '2') {
        this.zrgsRes = false;
        this.zrgsData = {
          dimension: xData,
          quotaMap: { '供电有限责任公司派单及时率': dataInfo },
          colorMap: { '供电有限责任公司派单及时率': ['#00FEBB', '#00BDFA'] }
        };
        this.$nextTick(() => {
          this.zrgsRes = true;
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.dispath-style {
  width: 100%;
  height: 100%;

  .title {
    color: #00e7ff;
    height: 23%;
    font-size: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .charts-style {
    height: 77%;
    width: calc(100% - 40px);

    padding: 0 20px;
    .left {
      width: 30%;
      float: left;
      height: 100%;

      display: flex;
      justify-items: center;
      align-items: center;

      .left-main {
        width: 100%;
        height: 100%;

        .kb-style-main {
          width: 100%;
          height: 45%;

          .kb-title {
            height: 20%;
            color: #fff;
            font-size: 18px;
            display: flex;
            justify-content: center;
            align-items: center;
          }

          .kb-value {
            width: 100%;
            height: 80%;
            color: #0fb0ff;
            font-weight: 600;
            font-size: 22px;
            span {
              display: flex;
              justify-content: center;
              align-content: center;
              align-items: center;
              width: 100%;
              height: 80%;
              background: url("../../../assets/img/业务总量框.png") no-repeat
                top center;
              background-size: 100% 100%;
            }
          }
        }
      }
    }

    .right {
      width: 70%;
      height: 100%;
      float: left;

      .legeng-style {
        height: 40px;
        float: right;
        margin-right: 20px;
        line-height: 40px;

        .tl-label {
          color: #fff;
          display: block;
          float: left;
          margin-left: 10px;

          .qk {
            width: 10px;
            height: 10px;
            display: block;
            float: left;
            margin-right: 5px;
            margin-top: 16px;
          }

          .qk-value {
            float: left;
          }
        }
      }

      .count-kb-style {
        width: 100%;
        height: calc(100% - 50px);
        overflow: auto;

        .kb-line {
          width: calc(50% - 16px);
          height: 25%;

          float: left;
          margin-right: 15px;
          cursor: pointer;
          display: flex;
          justify-content: center;
          align-items: center;

          .label-style {
            width: 40px;
            float: left;
            color: #fff;
            font-size: 15px;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
          }

          .count-zb-main {
            width: calc(100% - 50px);

            float: left;
            margin-top: -15px;

            .zb-details {
              .zb-cs {
                color: #9c92cf;
                font-weight: 600;
              }

              .zb-zl {
                color: #febb7a;
                font-weight: 600;
              }
            }
          }

          .count-kb-bg {
            height: 15px;
            background: #febb7a;
            /*border-radius: 10px;*/
            width: 100%;

            .count-zb-style {
              background: #9c92cf;
              height: 100%;
              /*border-radius: 15px;*/
            }
          }
        }
      }
    }
  }

  .bottom-left {
    width: 65%;
    float: left;
    height: 100%;

    .bottom-left-left {
      background: url("../../../assets/img/管理框4.png") no-repeat top center;
      background-size: 100% 100%;
      width: 55%;
      height: 100%;
      float: left;
    }

    .bottom-left-right {
      background: url("../../../assets/img/管理框5.png") no-repeat top center;
      background-size: 100% 100%;
      width: 45%;
      height: 100%;
      float: left;
    }
  }

  .bottom-right {
    background: url("../../../assets/img/管理框6.png") no-repeat top center;
    background-size: 100% 100%;
    width: 35%;
    float: left;
    height: 100%;
  }
}
</style>